@import '../../scss/variables';

.payment_container {
	width: $container-width;
	margin: 0 auto;
	margin: 15px auto;
	.payment__top {
		h2 {
			font-size: 22px;
			text-align: center;
			svg {
				position: relative;
				top: 6px;
				width: 30px;
				height: 30px;
				margin-right: 10px;
				path {
					stroke: $orange;
					fill: yellow;
				}
			}
			span {
				font-weight: 900;
				margin: 0 5px;
			}
		}
		div {
			font-size: 18px;
			margin: 30px;
			text-align: center;
		}
	}
	.payment__form {
		.inputs_wrapper {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			gap: 10%;
			@media (max-width: 1200px) {
				flex-direction: column;
				align-items: center;
				margin-bottom: 70px;
			}
			@media (max-width: 768px) {
				margin-bottom: 20px;
			}
		}

		.personal_info {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		.field {
			height: 89px;
			margin-bottom: 20px;

			label {
				display: block;
				font-weight: 900;
				font-size: 20px;
			}
			input {
				border: 1px solid rgba(0, 0, 0, 0.1);
				padding: 15px 15px;
				width: 350px;
				border-radius: 8px;
				font-size: 16px;
				box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
					rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

				@media (max-width: 768px) {
					width: 300px;
				}
				&:focus {
					border: 1px solid #e9a68b;
				}
			}

			.errors {
				color: #e11313;
				text-align: center;
			}
		}

		.card_info {
			position: relative;
			width: 350px;
			@media (max-width: 768px) {
				width: 300px;
			}

			> label {
				text-align: center;
				display: block;
				font-weight: 900;
				font-size: 20px;
				margin-bottom: 20px;
				@media (max-width: 768px) {
					margin-bottom: 5px;
				}
			}
			.field_card {
				position: relative;
				z-index: 2;
				height: 200px;
				padding: 30px 20px;
				border-radius: 10px;
				background-color: #3598db;
				box-shadow: rgb(38, 57, 77) 10px 20px 30px -10px;
				input {
					display: inline-block;
					font-size: 16px;
					font-weight: 800;
					letter-spacing: 4px;
					color: #702020;
					width: 100%;
					border-radius: 8px;
					padding: 10px 32px;
					border: 1px solid rgba(0, 0, 0, 0.1);
					box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
						rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
					@media (max-width: 768px) {
						padding: 10px 7px;
					}
				}

				.field_card_term {
					padding: 10px 18px;
					width: 100px;
					margin-top: 45px;
					letter-spacing: 1px;
				}

				.errors_card_number {
					position: absolute;
					color: #e11313;
					margin-left: 10px;
				}
				.errors_term {
					display: inline-block;
					color: #e11313;
					margin-left: 10px;
				}
			}
			.field_card_back {
				position: absolute;
				top: 85px;
				right: -140px;
				width: 350px;
				height: 200px;
				border-radius: 10px;
				background-color: #2c81ba;
				z-index: 1;
				box-shadow: rgb(38, 57, 77) 10px 20px 30px -10px;

				@media (max-width: 768px) {
					width: 300px;
					position: static;
					margin-top: 10px;
					padding: 8px 0;
				}

				p {
					background-color: #091d36;
					width: 100%;
					height: 45px;
					margin-top: 23px;
				}

				input {
					font-size: 20px;
					text-align: center;
					display: block;
					width: 20%;
					border-radius: 8px;
					padding: 10px 15px;
					border: 1px solid rgba(0, 0, 0, 0.1);
					margin-top: 30px;
					margin-left: 240px;
					box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
						rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
					@media (max-width: 768px) {
						margin-left: 200px;
						width: 70px;
					}
				}
				.errors_card_password {
					display: inline-block;
					color: #e11313;
					margin-left: 209px;
					margin-top: 5px;
					text-align: center;
					line-height: 12px;
				}
			}
		}

		button {
			position: relative;
			margin: 30px auto;
			display: block;
			svg {
				position: absolute;
				top: 19px;
				left: 30px;
			}
		}
	}

	.submit_error {
		color: #bb1b1b;
		text-align: center;
	}
}
